@import url(https://fonts.googleapis.com/css2?family=Nunito&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@700&display=swap);
.box {
    box-shadow: 0 1px 2px rgba(0, 0, 0, 0.1);
    transition: all 0.6s cubic-bezier(0.165, 0.84, 0.44, 1);
    font-family: 'Nunito', sans-serif;
  }
  
  .box::after {
    content: "";
    border-radius: 1rem;
    position: absolute;
    z-index: -1;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    box-shadow: 0 5px 20px rgba(0, 0, 0, 0.2);
    opacity: 0;
    transition: all 0.6s cubic-bezier(0.165, 0.84, 0.44, 1);
  }
  
  .box:hover {
    transform: scale(1.02, 1.02);
  }
  
  .box:hover::after {
      opacity: 1;
  }

  .timestamp {
      font-size: 13px;
  }
.fa-link {
    transform: rotate(-45deg);
    font-size: 35px;
    margin-top: 50px;
    margin-bottom: 50px;
}

@-webkit-keyframes swing
{
    15%
    {
        transform: translateX(5px) rotate(-45deg);
    }
    30%
    {
        transform: translateX(-5px) rotate(-45deg);
    }
    50%
    {
        transform: translateX(3px) rotate(-45deg);
    }
    65%
    {
        transform: translateX(-3px) rotate(-45deg);
    }
    80%
    {
        transform: translateX(2px) rotate(-45deg);
    }
    100%
    {
        transform: translateX(0) rotate(-45deg);
    }
}
@keyframes swing
{
    15%
    {
        transform: translateX(5px) rotate(-42deg);
    }
    30%
    {
        transform: translateX(-5px) rotate(-47deg);
    }
    50%
    {
        transform: translateX(3px) rotate(-42deg);
    }
    65%
    {
        transform: translateX(-3px) rotate(-47deg);
    }
    80%
    {
        transform: translateX(2px) rotate(-45deg);
    }
    100%
    {
        transform: translateX(0) rotate(-45deg);
    }
}

.swing:hover
{
    -webkit-animation: swing 1s ease;
    animation: swing 1s ease;
    -webkit-animation-iteration-count: 1;
    animation-iteration-count: 1;
    transform: rotate(-45deg);
}
.subtitle {
    margin: 5vh 10vw;
    letter-spacing: 0.06rem;
    padding: 4rem;
    line-height: 1.5rem;
}

.App {
  background-color: rgb(252, 252, 251);
  height: 1000px;
}

header {
  font-family: 'Raleway', sans-serif;
  text-transform: uppercase;
  letter-spacing: 10px;
  padding-top: 10rem;
  padding-bottom: 10rem;
  border-bottom-left-radius: 2rem;
  border-bottom-right-radius: 2rem;
}

header, footer, .bg-custom {
